
.csv-details-form-wrapper {
    margin-top: 15px;
    margin-bottom: 30px;
}

.csv-details-form_header div {
    display: inline-block;
    margin: 5px;
    vertical-align: middle;
}

.csv-details-form_buttons {
    margin-top: 25px;
}

.csv-details-form .delete-icon {
    cursor: pointer;
    margin: -5px 5px 0 5px;
}

.step-marker {
    width: 35px;
    height: 35px;
    background-color: #4890e1;
    line-height: 35px;
    text-align: center;
    border-radius: 50%;
    color: #fff;
}

.csv-details-form_container > div {
    display: flex;
    margin: 5px;
    vertical-align: top;
}

.csv-details-form_container .vertical-line {
    background-color: #9d9d9d;
    opacity: 0.75;
    margin: 0 17px;
    width: 1px;
    height: 100%;
}

.csv-details-form .dropdown-toggle {
    width: 100%;
    text-align: left;
}

.csv-details-form .dropdown-toggle::after {
    position: absolute;
    right: 10px;
    top: calc(50% - 2px);
}

.hint-required::after {
    content: "required";
    position: absolute;
    top: -10px;
    right: 10px;
    font-size: 75%;
    color: #6c757d;
}
