
.dropzone-container {
	margin: 5px;
}

.dropzone {
	flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-width: 2px;
    border-radius: 2px;
    border-color: #eeeeee;
    border-style: dashed;
    background-color: #fafafa;
    color: #bdbdbd;
    outline: none;
    transition: border .24s ease-in-out;
    height: 150px;
}

.dropzone p {
	line-height: 100px;
}

.dropzone:focus, .dropzone.dragged {
    border-color: #2196f3;
}

.dropzone.error {
    border-color: red;
}

.dropzone.success {
    border-color: green;
}

.dropzone-container .error-message {
	color: red;
	text-align: center;
	margin: 10px;
}

.dropzone-container .success-message {
	color: black;
}

.dropzone-container .success-message span {
	font-weight: 600;
}